import React from "react";
import { IconButton, Tooltip } from "@mui/material";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { useHistory } from "react-router-dom";

const DeficientPartToolTip = ({ children, data }) => {
  const history = useHistory();
  const goToMBForecastingPage = () => {
    localStorage.setItem("PTNo", data);
    history.push("/mb-forecasting");
  };
  const goSubAssemblyPage = () => {
    localStorage.setItem("PTNo", data);
    history.push("/sub-assembly");
  };
  return (
    <Tooltip
      title={
        <div
          style={{
            backgroundColor: "#fff",
            color: "#000",
            margin: "-5px -8px",
            boxShadow: "#0000005f 0px 1px 12px",
            borderRadius: "5px",
            fontWeight: "600",
          }}
        >
          <List dense>
            <ListItem
              disablePadding
              secondaryAction={
                <IconButton edge="end" aria-label="forward" onClick={() => goToMBForecastingPage()}>
                  <ArrowForwardIosIcon sx={{width:'20px', height:'20px'}} />
                </IconButton>
              }
            >
              <ListItemButton onClick={() => goToMBForecastingPage()}>
                <ListItemText primary="View ForeCast" />
              </ListItemButton>
            </ListItem>
            <ListItem disablePadding secondaryAction={
                <IconButton edge="end" aria-label="forward" onClick={() => goSubAssemblyPage()}>
                  <ArrowForwardIosIcon sx={{width:'20px', height:'20px'}} />
                </IconButton>
              }>
              <ListItemButton onClick={() => goSubAssemblyPage()}>
                <ListItemText primary="View Sub-Assembly" />
              </ListItemButton>
            </ListItem>
          </List>
        </div>
      }
      placement="top"
    >
      {children}
    </Tooltip>
  );
};

export default DeficientPartToolTip;
